import React, {Component} from 'react';
import './contact.css';

class Contact extends Component{
    render(){
        return(
            <div id="contact">
                <h3>CONTACT</h3>
                <div id="main" className="d-flex">
                    <div className="flex-center contact-div">
                        <img src="/contactus.png" style={{width: "100%"}}/>
                    </div>
                    <div className="flex-center contact-div">
                        <div id="emaildiv">
                            <h4>EMAIL ME!</h4>
                            <div className="input-group">
                                <label>Your Full Name *</label>
                                <input type="text" name="fullname" placeholder="Full Name"/>
                            </div>
                            <div className="input-group">
                                <label>Your Email Address *</label>
                                <input type="text" name="email" placeholder="Email Address"/>
                            </div>
                            <div className="input-group">
                                <label>Your Email Address *</label>
                                <textarea name="message" placeholder="Message" rows="10"></textarea>
                            </div>

                            <div className="d-flex justify-content-end" style={{padding: "0 5%"}}>
                                <button id="send-btn">SEND</button>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        )
    }
}

export default Contact;