import React, {Component} from 'react';
import './topnav.css';

class TopNav extends Component{
    constructor(props){
        super(props);
        this.homelinkRef = React.createRef();
        this.profilelinkRef = React.createRef();
        this.skillslinkRef = React.createRef();
        this.projectslinkRef = React.createRef();
        this.contactlinkRef = React.createRef();
        this.state = {
            showmobilenav: false
        }
    }
    render(){
        return (
            <div id="topnav" className="d-flex justify-content-end">
                <div className="links" ref={this.homelinkRef} onClick={this.props.homeClick}>HOME</div>
                <div className="links" ref={this.profilelinkRef} onClick={this.props.profileClick}>EXPERIENCE</div>
                <div className="links" ref={this.skillslinkRef} onClick={this.props.skillsClick}>SKILLS</div>
                <div className="links" ref={this.projectslinkRef} onClick={this.props.projectClick}>PROJECTS</div>
                <div className="links" ref={this.contactlinkRef} onClick={this.props.contactClick}>CONTACT</div>
                <div id="hmenu" onClick={()=>{
                    this.setState({showmobilenav: !this.state.showmobilenav});
                }} className={(this.state.showmobilenav) ? "show" : ""}>
                    <div className="line" id="line1"></div>
                    <div className="line" id="line2"></div>
                    <div className="line" id="line3"></div>
                </div>

                <div id="mobilenav" className={(this.state.showmobilenav) ? "show" : ""}>
                    <div id="dark-overlay"></div>
                    <div className="mobile-links" onClick={()=> {
                        this.setState({showmobilenav: false})
                        this.props.homeClick()}}>HOME</div>
                    <div className="mobile-links" onClick={()=> {
                        this.setState({showmobilenav: false})
                        this.props.profileClick()}}>EXPERIENCE</div>
                    <div className="mobile-links" onClick={()=> {
                        this.setState({showmobilenav: false})
                        this.props.skillsClick()}}>SKILLS</div>
                    <div className="mobile-links" onClick={()=> {
                        this.setState({showmobilenav: false})
                        this.props.projectClick()}}>PROJECTS</div>
                    <div className="mobile-links" onClick={()=>{
                        this.setState({showmobilenav: false})
                        this.props.contactClick()}}>CONTACT</div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        window.addEventListener('scroll', ()=>{
            const profileLocation = this.props.profileRef.current.offsetTop - 50;
            const skillsLocation = this.props.skillsRef.current.offsetTop - 50;
            const projectsLocation  = this.props.projectRef.current.offsetTop -100;
            const contactLocation = this.props.contactRef.current.offsetTop -50;
            
            let yoffset = window.pageYOffset;
            let maxheight = document.documentElement.scrollHeight - document.documentElement.clientHeight;
            if(yoffset < profileLocation){
                this.homelinkRef.current.classList.add('active');
                this.profilelinkRef.current.classList.remove("active");
                this.skillslinkRef.current.classList.remove("active");
                this.projectslinkRef.current.classList.remove("active");
                this.contactlinkRef.current.classList.remove("active");
            }
            else if(yoffset >= profileLocation && yoffset < skillsLocation){
                this.profilelinkRef.current.classList.add("active")
                this.homelinkRef.current.classList.remove("active");
                this.skillslinkRef.current.classList.remove("active");
                this.projectslinkRef.current.classList.remove("active");
                this.contactlinkRef.current.classList.remove("active");
            }
            else if (yoffset >= skillsLocation && yoffset < projectsLocation){
                this.skillslinkRef.current.classList.add("active")
                this.homelinkRef.current.classList.remove("active");
                this.profilelinkRef.current.classList.remove("active");
                this.projectslinkRef.current.classList.remove("active");
                this.contactlinkRef.current.classList.remove("active");
            }
            else if (yoffset >= projectsLocation && yoffset < maxheight){
                this.projectslinkRef.current.classList.add("active");
                this.homelinkRef.current.classList.remove("active");
                this.profilelinkRef.current.classList.remove("active");
                this.skillslinkRef.current.classList.remove("active");
                this.contactlinkRef.current.classList.remove("active");
            }
            else if (yoffset >= maxheight){
                this.contactlinkRef.current.classList.add("active");
                this.homelinkRef.current.classList.remove("active");
                this.profilelinkRef.current.classList.remove("active");
                this.skillslinkRef.current.classList.remove("active");
                this.projectslinkRef.current.classList.remove("active");
            }
        })
    }
}

export default TopNav;