import React, {Component} from 'react';
import './skills.css';
import Animate from './animate';

class Skills extends Component{
    constructor(props){
        super(props);
        this.animateRef = React.createRef();
    }
    render(){
        return(
            <div id="skills">
                <canvas id="skill-canvas" width={1000} height={1000} ref={this.animateRef}></canvas>
                {/* <div className="dark-overlay"/> */}
                <h3 id="header">SKILLS</h3>
                <div id="main" className="d-flex flex-wrap">
                    <div className="col-6" style={{paddingRight: '5px'}}>
                        <div className="skill-group">
                            <h5 className="skillglabel">FRONT-END</h5>
                            <div className="d-flex">
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/html5.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>HTML5</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/css.jpg" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>CSS3</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/javascript.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>JAVASCRIPT</div>
                                    </div>
                                </div>
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/react.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>REACT JS</div>
                                    </div>
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/bootstrap.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>BOOTSTRAP</div>
                                    </div>
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/jquery.gif" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>JQUERY</div>
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                    <div className="col-6" style={{paddingLeft: '5px'}}>
                        <div className="skill-group">
                            <h5 className="skillglabel">BACK-END</h5>
                            <div className="d-flex">
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/php.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>PHP</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/csharp.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>C#</div>
                                    </div>

                                </div>

                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/laravel.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>LARAVEL</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/asp.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>ASP.NET</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/node.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>NODE.JS</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-6" style={{paddingRight: '5px', paddingTop: '15px'}}>
                        <div className="skill-group">
                            <h5 className="skillglabel">DATABASE</h5>
                            <div className="d-flex">
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/mssql.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>MS SQL Server</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/mysql.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>MySQL</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/mongodb.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>MONGODB</div>
                                    </div>
                                </div>
                                <div className="flex-1">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-6" style={{paddingLeft: '5px', paddingTop: '15px'}}>
                        <div className="skill-group">
                            <h5 className="skillglabel">TOOLS</h5>
                            <div className="d-flex">
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/git.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>GIT</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/postman.jpg" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>POSTMAN</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/photoshop.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>PHOTOSHOP</div>
                                    </div>
                                </div>
                                <div className="flex-1">
                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/vscode.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>VISUAL STUDIO CODE</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/trello.jpg" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>TRELLO</div>
                                    </div>

                                    <div className="d-flex align-items-center">
                                        <div style={{padding: "5px 10px"}}><img src="/figma.png" style={{height: "30px", width:"30px"}}/></div>
                                        <div style={{fontWeight: "bolder"}}>FIGMA</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount() {
        this.context = this.animateRef.current.getContext('2d');
        Animate(this.context)
    }
}
export default Skills;