
var height = 1000;
var width = 1000;
var withLines = true;
var multiColors = false;
var circleSize = 5;
var randomSize = true;
var connectSize = 150;
var maxSize = 5 ;
var circleNum = 75;
var speed = 3.5;
var timeOut= 30;
var MainContext = ''
var Interval;
var allColors = ["#c62828", "#AD1457","#6A1B9A","#4527A0", "#283593", "#1565C0", "#0277BD", "#00838F",
  "#00695C", "#2E7D32", "#558B2F", "#9E9D24", "#F9A825", "#FF8F00", "#EF6C00", "#D84315", "#4E342E"]

var circles = []

function Init () {
  circles = [];
  circles = Array(circleNum)
  .fill(null)
  .map(randomPosition);

    circles.forEach(c => {
        c.color = "rgba(250, 250, 250, .3)"
        if (multiColors){
          var randomColorIndex = Math.floor(Math.random() * allColors.length)
          c.color = allColors[randomColorIndex];
        }
        if (randomSize){
          c.size = Math.floor(Math.random() * maxSize)
        }
        else{
          c.size = circleSize
        }
        createCircle(c.x, c.y, c.color, c.size);
      });
}

function randomPosition() {
  const x = Math.floor(Math.random() * width);
  const y = Math.floor(Math.random() * height);
  const negX = Math.floor(Math.random() * 5);
  const negY = Math.floor(Math.random() * 5);
  const yPos =
    negY > 2
      ? Math.floor(Math.random() * speed) * -1
      : Math.floor(Math.random() * speed);
  const xPos =
    negX > 2
      ? Math.floor(Math.random() * speed) * -1
      : Math.floor(Math.random() * speed);
  return { x: x, y: y, xpos: xPos, ypos: yPos, negX, negY };
}

function createCircle(x, y, color, size) {
  var ctx = MainContext;
  if (x === 0) x = 1;
  if (y === 0) y = 1;
  const conCircles = circles.filter(c => {
    if (((c.x <= x + connectSize && c.x >= x) || (c.x >= x - connectSize && c.x <= x))
        && ((c.y <= y + connectSize && c.y >= y) || (c.y >= y - connectSize && c.y <= y))
        // && (!c.connected)
        ) {
      return c;
    }
  });
  if (withLines){
    conCircles.forEach(xc => {
      ctx.moveTo(x, y);
      ctx.lineTo(xc.x, xc.y);
      var opacity = '0.3'
      
      ctx.strokeStyle = "rgb(250, 250, 250, .2)"
      ctx.lineWidth = .2;
      ctx.stroke();
    });
  }
  
  ctx.beginPath();
  ctx.arc(x, y, size, 0, 2 * Math.PI);
  ctx.fillStyle = color
  ctx.strokeStyle = "rgba(250, 250, 250, 0)"
  ctx.fill();
  ctx.stroke();
}
const Run = (context)=> {
    clearInterval(Interval);
    MainContext = context;
    Init();
    Interval = setInterval(() => {
        var ctx = MainContext
        ctx.clearRect(0, 0, width, height);
        circles.forEach(c => {
          if (c.x <= 0 || c.x >= width) c.xpos = c.xpos * -1;
          if (c.y <= 0 || c.y >= height) c.ypos = c.ypos * -1;
          c.x = c.x + c.xpos;
          c.y = c.y + c.ypos;
          createCircle(c.x, c.y, c.color, c.size);
        })
      }, timeOut);
}

export default Run;