import React, {Component} from 'react';
import './profile.css';

class Profile extends Component{
    render(){
        return(
            <div id="profile">
                <div id="exp" className="flex-1">
                    <h3>WORK EXPERIENCE</h3>
                    <br/>
                    {/* JEONSOFT CORP */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Jeonsoft Corporation</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="line"></div></div>
                        <div className="d-flex flex-row">
                            <div className="position">Senior Software Developer</div>
                            <div className="daterange">October 2015 - April 2019</div>
                        </div>
                    </div>
                    {/* DBP */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Development Bank of The Philippines</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="line"></div></div>
                        <div className="d-flex flex-row">
                            <div className="position">Programmer I</div>
                            <div className="daterange">April 2019 - September 2019</div>
                        </div>
                    </div>
                    {/* Sqreem Technologies */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Sqreem Technologies</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="line"></div></div>
                        <div className="d-flex flex-row">
                            <div className="position">Software Engineer</div>
                            <div className="daterange">September 2019 - January 2020</div>
                        </div>
                    </div>
                    {/* Bureau of Customs */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Bureau of Customs</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"></div>
                        <div className="d-flex flex-row">
                            <div className="position">Programmer</div>
                            <div className="daterange">July 2020 - Present</div>
                        </div>
                    </div>
                </div>
                <div id="educ" className="flex-1">
                    <h3>EDUCATION</h3>
                    <br/>
                    {/* Elementary */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">A.L. Navarro Central Elementary School</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="line"></div></div>
                        <div className="d-flex flex-row">
                            <div className="position">Elementary</div>
                            <div className="daterange">Graduated Year 2006</div>
                        </div>
                    </div>
                    {/* Highschool */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Tandang Sora National Highschool</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="line"></div></div>
                        <div className="d-flex flex-row">
                            <div className="position">Highschool</div>
                            <div className="daterange">Graduated Year 2010</div>
                        </div>
                    </div>
                    {/* College */}
                    <div className="d-flex">
                        <div className="flex-center timeline"><div className="circ"></div></div>
                        <div className="company">Quezon City Polytechnic University</div>
                    </div>
                    <div className="d-flex">
                        <div className="flex-center timeline"></div>
                        <div className="d-flex flex-row">
                            <div className="position">Bachelor of Science in Information Technology</div>
                            <div className="daterange">Graduated Year 2015</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Profile;