import React, {Component} from 'react';
import './hero.css';

class Hero extends Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div id="hero">
                <div id="main" className="d-flex align-items-center">
                    <div className="flex-1 flex-center prof-div">
                        <div id="profilepic" className="flex-center" style={{borderRadius: "50%", backgroundColor: "black", height: "300px", width: "300px", overflow:"hidden", backgroundColor: "#034078", border: "1px solid white"}}>
                            <img src="/prof.png" style={{height: "500px"}}/>
                        </div>
                    </div>
                    <div className="flex-1 d-flex justify-content-center flex-row prof-div">
                        <h2>HI! I am Alexies Toshihiko Song</h2>
                        <h2>I am a Fullstack Software Developer.</h2>
                        <div id="desc-container">
                            <p style={{padding: '10px'}}>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p>
                        </div>
                        <div className="d-flex justify-content-center" style={{padding: '10px'}}>
                            <button id="btn-learn" onClick={this.props.profileClick}>LEARN MORE</button>
                        </div>
                    </div>
                </div>
                
            </div>
        )
    }
}

export default Hero;