import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import TopNav from './component/topnav';
import Hero from './component/hero';
import Profile from './component/profile';
import Skills from './component/skills';
import Project from './component/projects';
import Contact from './component/contact';
import Footer from './component/footer';

class App extends Component {
  constructor(){
    super();
    this.appRef = React.createRef();
    this.topNavRef = React.createRef();
    this.profileRef  = React.createRef();
    this.skillsRef = React.createRef();
    this.projectRef = React.createRef();
    this.contactRef = React.createRef();
    this.homeClick = this.homeClick.bind(this);
    this.profileClick = this.profileClick.bind(this);
    this.skillsClick = this.skillsClick.bind(this);
    this.projectClick = this.projectClick.bind(this);
    this.contactClick = this.contactClick.bind(this);
  }
  homeClick(){
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth'
    })
  }
  profileClick(){
    const profileLocation = this.profileRef.current.offsetTop - 50;
    window.scrollTo({
      top: profileLocation,
      left: 0,
      behavior: 'smooth'
    })
  }
  skillsClick(){
    const skillsLocation = this.skillsRef.current.offsetTop  - 50;
    window.scrollTo({
      top: skillsLocation,
      left: 0,
      behavior: 'smooth'
    })
  }
  projectClick(){
    const projectLocation = this.projectRef.current.offsetTop  - 50;
    window.scrollTo({
      top: projectLocation - 20,
      left: 0,
      behavior: 'smooth'
    })
  }
  contactClick(){
    const contactLocation = this.contactRef.current.offsetTop  - 50;
    window.scrollTo({
      top: contactLocation,
      left: 0,
      behavior: 'smooth'
    })
  }
  render(){
    return (
      <div className="App" ref={this.appRef}>
        <div ref={this.topNavRef}>
          <TopNav
            homeClick={this.homeClick}
            profileClick={this.profileClick}
            skillsClick={this.skillsClick}
            projectClick={this.projectClick}
            contactClick={this.contactClick}
            appRef={this.appRef}
            profileRef = {this.profileRef}
            skillsRef = {this.skillsRef}
            projectRef = {this.projectRef}
            contactRef = {this.contactRef}/>
          </div>
          <Hero ref={this.heroRef}
            profileClick={this.profileClick}/>
          <div className="container" ref={this.profileRef}>
              <Profile />
          </div>
          <div ref={this.skillsRef}>
            <Skills />
          </div>
          <div className="container">
              <div ref={this.projectRef}><Project/></div>
              <div ref={this.contactRef}><Contact/></div>
          </div>
          <Footer/>
      </div>
    );
  }
  componentDidMount(){
    window.onscroll = ()=> {
      if(window.pageYOffset > 500){
        this.topNavRef.current.classList.add("topnavbar-fixed");
      }
      else{
        this.topNavRef.current.classList.remove("topnavbar-fixed");
      }
    }
  }
}

export default App;
